<template>
  <div class="register-container">
    <Card style="overflow: auto;" :shadow="false">
      <form
        @submit.prevent="handleSubmit"
        name="contacto"
        method="post"
        class="forms-container"
        data-netlify="true"
        netlify-honeypot="bot-field"
      >
        <input name="bot-field" hidden />
        <div class="column-container">
          <div class="column-container">
            <div class="divisor">
              <span class="divisor-text">Informacion de contacto</span>
            </div>
            <div class="row-container">
              <customInput
                v-model="form.Nombre"
                v-bind="{
                identifier: 'Nombre',
                label: 'Nombre',
                icon: 'user',
                type: 'text'
              }"
              />

              <customInput
                v-model="form.Apellido"
                v-bind="{
                identifier: 'Apellido',
                label: 'Apellido',
                icon: 'user-o',
                type: 'text',
              }"
              />
            </div>

            <div class="row-container">
              <customInput
                v-model="form.Telefono"
                v-bind="{
                identifier: 'Telefono',
                label: 'Telefono',
                icon: 'phone',
                type: 'phone'
              }"
              />
              <customInput
                v-model="form.email"
                v-bind="{
                identifier: 'email',
                label: 'Correo electronico',
                icon: 'envelope',
                type: 'email'
              }"
              />
            </div>
            <div class="row-container">
              <customInput
                v-model="form.Compa√±ia"
                v-bind="{
                identifier: 'Compa√±ia',
                label: 'Compa√±ia',
                icon: 'building',
                type: 'text',
                required: false
              }"
              />
            </div>
          </div>
          <div class="column-container">
            <div class="divisor">
              <span class="divisor-text">Informacion del evento</span>
            </div>

            <div class="row-container">
              <customInput
                v-model="form.TipoDeEvento"
                v-bind="{
                identifier: 'TipoDeEvento',
                label: 'Tipo de evento',
                icon: 'gift',
                type: 'text'
              }"
              />
            </div>

            <div class="row-container">
              <customInput
                v-model="form.Fecha"
                v-bind="{
                identifier: 'Fecha',
                label: 'Fecha',
                icon: 'calendar',
                type: 'text',
              }"
              />

              <customInput
                v-model="form.NumeroDePersonas"
                v-bind="{
                identifier: 'NumeroDePersonas',
                label: 'Numero de personas',
                icon: 'hashtag',
                type: 'text',
              }"
              />
            </div>

            <div class="row-container">
              <customInput
                v-model="form.UbicacionDelEvento"
                v-bind="{
                identifier: 'UbicacionDelEvento',
                label: 'Ubicacion del evento',
                icon: 'map-marker',
                type: 'text',
              }"
              />
            </div>
          </div>
        </div>
        <div class="column-container">
          <customtextarea
            v-model="form.detalles"
            v-bind="{
            identifier: 'detalles',
            label: 'Detalles',
          }"
          />
        </div>
        <button class="boton-enviar">
          ENVIAR
          <i class="fa fa-paper-plane"></i>
        </button>
      </form>
    </Card>
    <transition name="FadeIn">
      <div
        class="message-contacto"
        v-show="mostrar_mensaje"
      >Informacion enviada exitosamente, nos comunicaremos con usted en la brevedad posible.</div>
    </transition>
  </div>
</template>

<script>
import customInput from "@/components/custominput/custominput";
import customtextarea from "@/components/customtextarea/customtextarea";
import Card from "@/components/card/card";

export default {
  name: "contact_view",
  components: {
    customInput,
    customtextarea,
    Card
  },
  data() {
    return {
      form: {
        Nombre: "",
        Apellido: "",
        Compa√±ia: "",
        email: "",
        Telefono: "",
        TipoDeEvento: "",
        Fecha: "",
        NumeroDePersonas: "",
        UbicacionDelEvento: "",
        detalles: ""
      },
      mostrar_mensaje: {
        type: Boolean,
        default: false
      }
    };
  },
  methods: {
    encode(data) {
      return Object.keys(data)
        .map(
          key => `${encodeURIComponent(key)}=${encodeURIComponent(data[key])}`
        )
        .join("&");
    },
    showMessage() {
      this.mostrar_mensaje = true;
      setTimeout(() => {
        this.mostrar_mensaje = false;
      }, 4000);
      for (const key in this.form) {
        this.form[key] = "";
      }
    },
    handleSubmit() {
      fetch("/", {
        method: "post",
        headers: {
          "Content-Type": "application/x-www-form-urlencoded"
        },
        body: this.encode({
          "form-name": "contacto",
          ...this.form
        })
      })
        .then(() => this.showMessage())
        .catch(e => console.error(e));
    }
  },
  beforeMount() {
    this.mostrar_mensaje = false;
  }
};
</script>

<style src="./contacto.scss" lang="scss" scoped></style>