<template>
  <div class="salados-container">
    <div class="center-content">
      <h2 class="titles">LISTA DE CANAPES Y PASAPALOS SALADOS</h2>
      <ul class="lista-salados-main">
        <li v-for="(value, index) in salados.salados" v-bind:key="index">{{value}}</li>
      </ul>
    </div>

    <div class="left-content">
      <h2 class="titles">PASTELITOS</h2>
      <ul class="lista-salados-simple">
        <li v-for="(value, index) in salados.pastelitos" v-bind:key="index">{{value}}</li>
      </ul>

      <h2 class="titles">AREPITAS</h2>
      <ul class="lista-salados-simple">
        <li v-for="(value, index) in salados.arepitas" v-bind:key="index">{{value}}</li>
      </ul>

      <h2 class="titles">MINI PIZZAS</h2>
      <ul class="lista-salados-simple">
        <li v-for="(value, index) in salados.pizzas" v-bind:key="index">{{value}}</li>
      </ul>

      <h2 class="titles">MINI HAMBURGUESAS</h2>
      <ul class="lista-salados-simple">
        <li v-for="(value, index) in salados.hamburguesas" v-bind:key="index">{{value}}</li>
      </ul>
    </div>

    <div class="right-content">
      <h2 class="titles">EMPANADITAS</h2>
      <ul class="lista-salados-simple">
        <li v-for="(value, index) in salados.empanaditas" v-bind:key="index">{{value}}</li>
      </ul>

      <h2 class="titles">TAPAS</h2>
      <ul class="lista-salados-simple">
        <li v-for="(value, index) in salados.tapas" v-bind:key="index">{{value}}</li>
      </ul>

      <h2 class="titles">PROFITEROLES</h2>
      <ul class="lista-salados-simple">
        <li v-for="(value, index) in salados.profiteroles" v-bind:key="index">{{value}}</li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  name: "salados",
  data() {
    return {
      salados: {
        type: JSON
      }
    };
  },
  methods: {
    async leerSalados() {
      await fetch(`salados.json`)
        .then(resp => resp.json())
        .then(json => {
          this.salados = json;
          this.$storage.set("Salados", JSON.stringify(this.salados));
        });
    }
  },
  beforeMount() {
    let cachedData = this.$storage.get("Salados", null);

    if (!cachedData) {
      this.leerSalados();
    } else {
      this.salados = JSON.parse(cachedData);
    }
  }
};
</script>

<style src="./salados.scss" lang="scss" scoped></style>