<template>
  <transition name="slideIn">
    <aside v-show="showSidebarComponent" class="sidebar-container">
      <transition name="slideIn">
        <div v-show="showSidebarComponent" class="sidebar" v-touch:swipe.left="closeSidebar">
          <linklist />
        </div>
      </transition>
      <transition name="FadeIn">
        <div v-show="showSidebarComponent" class="spacer" v-on:click="closeSidebar()"></div>
      </transition>
    </aside>
  </transition>
</template>

<script>
import linklist from "@/components/linklist/linklist";

export default {
  name: "Sidebar",
  components: {
    linklist
  },
  props: {
    showSidebarComponent: Boolean
  },
  methods: {
    closeSidebar() {
      this.$emit("clicked", this.showSidebarComponent);
    }
  }
};
</script>

<style src="./sidebar.scss" lang="scss" scoped></style>