<template>
  <div class="dulces-container">
    <div class="center-content">
      <h2 class="titles">LISTA DE DULCES</h2>
      <ul class="lista-dulces-main">
        <li v-for="(value, index) in dulces.dulces" v-bind:key="index">{{value}}</li>
      </ul>
    </div>

    <div class="left-content">
      <h2 class="titles">DULCES DECORADOS</h2>
      <ul class="lista-dulces-simple">
        <li v-for="(value, index) in dulces.decorados" v-bind:key="index">{{value}}</li>
      </ul>
    </div>

    <div class="right-content">
      <h2 class="titles">SHOTS DE DULCES</h2>
      <ul class="lista-dulces-simple">
        <li v-for="(value, index) in dulces.shots" v-bind:key="index">{{value}}</li>
      </ul>
    </div>
  </div>
</template>

<script>
export default {
  name: "dulces",
  data() {
    return {
      dulces: {
        type: JSON
      }
    };
  },
  methods: {
    async leerDulces() {
      await fetch(`dulces.json`)
        .then(resp => resp.json())
        .then(json => {
          this.dulces = json;
          this.$storage.set("Dulces", JSON.stringify(this.dulces));
        });
    }
  },
  beforeMount() {
    let cachedData = this.$storage.get("Dulces", null);

    if (!cachedData) {
      this.leerDulces();
    } else {
      this.dulces = JSON.parse(cachedData);
    }
  }
};
</script>

