import Vue from "vue";
import VueRouter from "vue-router";
import home from "@/views/home/home";
import eventos from "@/views/eventos/eventos";
import galeria from "@/views/galeria/galeria";
import dulces from "@/views/dulces/dulces";
import salados from "@/views/salados/salados";
import sabores from "@/views/sabores/sabores";

Vue.use(VueRouter);

const routes = [{
    path: "/",
    name: "home",
    component: home
  },
  {
    path: "/eventos",
    name: "eventos",
    component: eventos
  },
  {
    path: "/galeria",
    name: "galeria",
    component: galeria
  },
  {
    path: "/dulces",
    name: "dulces",
    component: dulces
  },
  {
    path: "/salados",
    name: "salados",
    component: salados
  },
  {
    path: "/sabores",
    name: "sabores",
    component: sabores
  }
];

const router = new VueRouter({
  mode: 'history',
  scrollBehavior() {
    return {
      x: 0,
      y: 0
    };
  },
  routes
});

export default router;