<template>
  <div
    class="carousel-container"
    :class="{'show-frame': show_frame, 'shadow': shadow}"
    :style="{width: carousel_width, height:carousel_height}"
    v-touch:swipe.left="goRight"
    v-touch:swipe.right="goLeft"
    v-on:mouseover="allowNext = false"
    v-on:mouseout="allowNext = true"
  >
    <div
      ref="carousel"
      class="carousel-content"
      :class="{'Hover': srcLink ? true : false}"
      v-on:click="$router.push(srcLink)"
    >
      <transition
        v-for="(_, slot) of $slots"
        v-bind:key="slot"
        v-bind:name="direction? 'slideInR' : 'slideInL'"
        mode="in-out"
      >
        <div class="carousel-item" v-show="slot == currentIndex">
          <slot :name="slot"></slot>
        </div>
      </transition>
    </div>
    <div class="controls">
      <button
        v-on:click="goLeft()"
        :disabled="currentIndex == 1"
        class="carousel-button"
        aria-label="Go left"
      >
        <i class="fa fa-angle-left"></i>
      </button>
      <button
        v-on:click="goRight()"
        :disabled="currentIndex == countItems"
        class="carousel-button"
        aria-label="Go Right"
      >
        <i class="fa fa-angle-right"></i>
      </button>
    </div>
  </div>
</template>

<script>
export default {
  name: "Carousel",
  props: {
    carousel_width: {
      type: String,
      default: "100%"
    },
    carousel_height: {
      type: String,
      default: "300px"
    },
    show_frame: {
      type: Boolean,
      default: false
    },
    carousel_time: {
      type: Number,
      default: 7000
    },
    shadow: {
      type: Boolean,
      default: false
    },
    iterCount: {
      type: Number,
      default: 0
    },
    srcLink: {
      type: String,
      default: null
    }
  },
  data() {
    return {
      currentIndex: {
        type: Number,
        default: 1
      },
      countItems: {
        type: Number,
        default: 0
      },
      direction: Boolean,
      allowNext: {
        type: Boolean,
        default: false
      },
      goingBack: {
        type: Boolean,
        default: false
      }
    };
  },
  methods: {
    goRight(param) {
      if (param && this.currentIndex != this.countItems) {
        this.currentIndex += 1;
      } else if (!param) {
        this.currentIndex += 1;
      }
      this.direction = true;
    },
    goLeft(param) {
      if (param && this.currentIndex != 1) {
        this.currentIndex -= 1;
      } else if (!param) {
        this.currentIndex -= 1;
      }
      this.direction = false;
    },
    goNext() {
      if (this.allowNext) {
        if (this.currentIndex != this.countItems && !this.goingBack) {
          this.currentIndex += 1;
          this.direction = true;
          setTimeout(() => {
            this.goNext();
          }, this.carousel_time);
        } else {
          this.direction = false;
          this.goingBack = true;
          setTimeout(() => {
            if (this.currentIndex != 1) {
              this.currentIndex -= 1;
              this.goNext();
            } else {
              this.currentIndex = 1;
              this.goingBack = false;
              setTimeout(() => {
                this.goNext();
              }, this.carousel_time);
            }
          }, 150);
        }
      } else {
        setTimeout(() => {
          this.goNext();
        }, this.carousel_time);
      }
    }
  },
  mounted() {
    this.currentIndex = 1;
    this.countItems = 0;
    this.direction = true;

    for (const key in this.$slots) {
      if (!isNaN(key)) {
        this.countItems += 1;
      }
    }

    if (this.countItems < 1) {
      if (this.iterCount) {
        this.countItems = this.iterCount;
      }
    }

    setTimeout(() => {
      this.goNext();
    }, this.carousel_time);
  }
};
</script>

