<template>
  <div class="dropdown-container">
    <button @click="showList = !showList" @focusout="focusOut" class="menu-button">
      <strong>PASAPALOS</strong>
    </button>
    <transition name="ScaleIn">
      <div v-if="showList" class="menu">
        <div v-for="(value, key) in options" v-bind:key="key" class="div-repetitivo">
          <router-link class="menu-item" :to="value[1]">{{value[0]}}</router-link>
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
export default {
  name: "dropdown",
  props: {
    options: {
      type: Object,
      default: null
    }
  },
  data() {
    return {
      showList: {
        type: Boolean,
        default: false
      }
    };
  },
  methods: {
    focusOut() {
      if (window.matchMedia("(min-width: 645px)").matches) {
        setTimeout(() => {
          this.showList = false;
        }, 100);
      }
    }
  },
  beforeMount() {
    this.showList = false;
  }
};
</script>

